.Node{

    width:50px;
    height:50px;
    display: inline-block;
    outline:1px solid rgb(197, 116, 9);
    
}

.node-finish{
    background-image: url(../Styling/end-target.png);
    background-size: contain;
}

.node-start{
     
    background-image: url(../Styling/start-arrow.png);
    background-size: contain;
}

.node-visited{
    background-color: rgb(81, 223, 216);
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    
}

.node-shortestPath{
    background-color: rgb(255, 254, 106);
    animation-name: shortestPath;
    animation-duration: 1.5s;
}

.node-wall{
    background-color: black;
}

@keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      background-color: rgba(0, 0, 66, 0.75);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgba(17, 104, 217, 0.75);
    }
  
    75% {
      transform: scale(1.2);
      background-color: rgba(0, 217, 159, 0.75);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(81, 223, 216);
    }
  }
  
 
  /* .node-shortest-path {
    animation-name: shortestPath;
